/*==================================================================
Reset & Default Setting CSS.
===================================================================*/
a,
address,
article,
aside,
audio,
blockquote,
body,
canvas,
caption,
code,
dd,
div,
dl,
dt,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
iframe,
img,
input,
label,
li,
mark,
menu,
nav,
ol,
output,
p,
pre,
ruby,
section,
span,
summary,
table,
tbody,
td,
textarea,
th,
time,
tr,
ul,
video {
  margin: 0;
  padding: 0;
  vertical-align: baseline;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  font-style: normal;
}

html {
  width: 100%;
  height: 100%;
}

body {
  position: relative;
  /* Smart Phone Smooth Scroll */
  -webkit-overflow-scrolling: touch;
  width: 100%;
  height: 100%;
  /*16px*/
  color: #000;
  background: #fff;
  font-family: "Helvetica Neue", Helvetica, arial, "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, Osaka, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
  font-size: 100%;
  line-height: 1;
  /* iOS font automatic adjustment none. */
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

.clear {
  clear: both;
}

.clearfix:after {
  display: block;
  clear: both;
  content: " ";
}

a {
  text-decoration: none;
  color: #000;
  background: transparent;

  &:active,
  &:hover,
  &:link,
  &:visited {
    text-decoration: none;
    color: #000;
  }

  &:hover {
    color: inherit;
    /*親要素の継承*/
    filter:progid:DXImageTransform.Microsoft.Alpha(Opacity=40);
    /* On Hold
    opacity: 0.4;
    */
  }
}

img {
  vertical-align: middle;
}

li,
ol,
ul {
  list-style-type: none;
}

table {
  table-layout: fixed;
  border-spacing: 0;
  border-collapse: collapse;
}

textarea {
  resize: vertical;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -ms-box-sizing: border-box;
}

::selection {
  background: RGBa(204, 0, 0, 0.1);
}

::-moz-selection {
  background: RGBa(204, 0, 0, 0.1);
}

.displayPC {
  display: block;
}

.displaySP {
  display: none;
}
