/*==================================================================
All The Small Things, Top Page.
===================================================================*/
html {
  overflow-x: hidden;
}

body {
  overflow-x: hidden;
  text-align: center;
}

#contents1 {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  margin-top: -30px;
  opacity: 0;
}

#contents2 {
  position: absolute;
  z-index: 0;
  display: none;
  width: 100%;
  height: 100%;
}

header {
  position: relative;
  height: 380px;

  h1#mainLogo {
    -webkit-transition: -webkit-transform 0.4s;
    -ms-transition: -ms-transform 0.4s;
    transition: transform 0.4s;
    /* IE and Old iPhone Introduction Animations Started Property*/
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
    opacity: 0;

    &.IEandOldIphoneIntroAnimations {
      -webkit-transition: -webkit-transform 1s;
      -ms-transition: -ms-transform 1s;
      transition: transform 1s;
      /* IE and Old iPhone Introduction Animations End Property*/
      -webkit-transform: scale(0.85);
      -ms-transform: scale(0.85);
      transform: scale(0.85);
    }

    a {
      display: inline-block;
      margin: 60px 0 0;
      -webkit-transition: all 0.2s linear;
      -moz-transition: all 0.2s linear;
      -ms-transition: all 0.2s;
      transition: all 0.2s linear;
      -webkit-transform: scale(1);
      -moz-transform: scale(1);
      -ms-transform: scale(1);
      transform: scale(1);
      opacity: 1;
      color: #222;
      text-shadow: 5px 5px 5px rgba(0, 0, 0, 0.45);
      font-family: 'Schoolbell', cursive;
      font-size: 10em;
      font-weight: normal;
      /*filter: blur(0px);*/
      &:hover {
        -webkit-transition: all 0.45s linear;
        -moz-transition: all 0.45s linear;
        -ms-transition: all 0.45s linear;
        transition: all 0.45s linear;
        //-webkit-transform: scale(0.75);
        //-moz-transform: scale(0.75);
        //-ms-transform: scale(0.75);
        //transform: scale(0.75);
        //letter-spacing: -0.04em;
        //opacity: 0.45;
        //text-shadow: none;
        line-height: 0.8;
        /*filter: blur(5px);*/
      }
      @-moz-document url-prefix() {
        /* FireFox Hack */
        &:hover {
          letter-spacing: 0;
        }
      }
    }
  }

  p {
    position: absolute;
    z-index: -1;
    bottom: 20px;
    //display: none;
    width: 100%;
    transition: opacity 1s linear;
    transition: transform 1s linear;
    transform: translate(0,0);
    text-align: center;
    letter-spacing: 0.05em;
    opacity: 0;
    color: #ff4e4e;
    font-family: 'Patrick Hand',cursive;
    font-size: 22px;
    font-weight: bold;
    font-style: italic;
    @media screen and (-webkit-min-device-pixel-ratio: 0) {
      /* Safari Hack */
      letter-spacing: 0;
    }
    @media all and (-webkit-min-device-pixel-ratio: 0) and (min-resolution: 0.001dpcm) {
      /* Chrome Hack */
      letter-spacing: 0.05em;
    }

    &.showAndMove {
      transition: opacity 2s;
      transition: transform 5s;
      transform: translate(0,100%);
      opacity: 1;
    }
  }
}
@media all and (-ms-high-contrast: none) {
  /* IE Hack */
  header h1#mainLogo a {
    -webkit-transition: all 0.2s;
    -ms-transition: all 0.2s;
    transition: all 0.2s;
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);

    &:hover {
      -webkit-transition: all 1s;
      -ms-transition: all 1s;
      transition: all 1s;
      -webkit-transform: scale(0.85);
      -ms-transform: scale(0.85);
      transform: scale(0.85);
    }
  }
}

#mainDescription {
  position: relative;
  width: 320px;
  height: 30px;
  margin: 70px auto 0;

  h2 {
    color: #555;
    font-family: sans-serif;

    &#kojiyamauchi {
      position: absolute;
      z-index: 3;
      right: 0;
      left: 0;
      width: 320px;
      height: 30px;
      line-height: 30px;

      span {
        position: relative;
        font-size: 1.125em;
        font-weight: bold;

        &#last {
          display: inline-block;

          &.comp {
            animation: rotateY 0.1s linear 0s infinite normal;
          }
        }
        @keyframes rotateY {
          0% {
            transform: rotateY(0deg);
          }

          100% {
            transform: rotateY(360deg);
          }
        }
      }
    }

    &#myPortfolio {
      position: absolute;
      z-index: 2;
      right: 0;
      left: 0;
      width: 320px;
      height: 30px;
      line-height: 30px;

      span {
        position: relative;
        top: 30px;
        opacity: 0;
        font-size: 1.125em;
        font-weight: bold;
      }
    }
  }

  #mainDescriptionBackIMG img {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 30px;
    height: 30px;
    opacity: 0;
    filter:progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  }
}

#subDescription {
  position: relative;
  width: 500px;
  height: 30px;
  margin: 15px auto 0;

  h3 {
    position: absolute;
    z-index: 1;
    right: 0;
    left: 0;
    width: 500px;
    height: 30px;
    color: #7a797a;
    font-family: 'Patrick Hand', cursive;
    font-size: 1.25em;
    font-style: italic;
    line-height: 30px;
  }

  #subDescriptionBackIMG img {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 30px;
    height: 30px;
    opacity: 0;
    filter:progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  }
}

#socialLink {
  margin: 48px 0 0;

  h4 {
    color: #555;
    font-family: sans-serif;
    font-size: 1.125em;
    font-weight: bold;

    span {
      font-weight: bold;
    }
  }

  ul {
    display: table;
    margin: 20px auto 0;

    li {
      display: table-cell;

      a {
        position: relative;
        display: inline-block;
        height: 40px;
        padding: 0 35px 0 55px;
        color: #555;
        background-repeat: no-repeat;
        background-position: left center;
        background-size: auto 100%;
        font-size: 0.875em;
        font-weight: bold;
        line-height: 40px;

        &::before {
          position: absolute;
          width: 40px;
          height: 40px;
          margin: 0 0 0 -55px;
          content: "";
          transform: scale(1);
          filter: brightness(1);
          opacity: 0;
        }

        &.pc {
          transition: all 0.5s;
          transform: scale(1);

          &:hover {
            transition: all 0.65s;
            transform: scale(1.45);

            &::before {
              animation: fireworks 0.45s ease-out 0.2s 1 normal;
              @keyframes fireworks {
                0% {
                  transform: scale(1);
                  opacity: 0;
                }

                50% {
                  opacity: 1;
                }

                100% {
                  transform: scale(2.5);
                  filter: brightness(1.25);
                  opacity: 0;
                }
              }
            }
          }
        }

        &.sp {
          &:hover {}
        }
      }

      &:nth-child(1) a {
        background-image: url("../images/iconTwitter.svg");

        &::before {
          background: url("../images/twitterFireWorks.svg") no-repeat;
        }
      }

      &:nth-child(2) a {
        background-image: url("../images/iconFaceBook.svg");

        &::before {
          background: url("../images/faceBookFireWorks.svg") no-repeat;
        }
      }

      &:nth-child(3) a {
        background-image: url("../images/iconLinkedIn.svg");

        &::before {
          background: url("../images/linkedInFireWorks.svg") no-repeat;
        }
      }

      &:nth-child(4) a {
        background-image: url("../images/iconBehance.svg");

        &::before {
          background: url("../images/behanceFireWorks.svg") no-repeat;
        }
      }

      &:nth-child(5) a {
        background-image: url("../images/iconGitHub.svg");

        &::before {
          background: url("../images/gitHubFireWorks.svg") no-repeat;
        }
      }

      &:nth-child(6) a {
        padding: 0 0 0 50px;
        background-image: url("../images/iconInstagram.svg");

        &::before {
          margin: 0 0 0 -50px;
          background: url("../images/instagramFireWorks.svg") no-repeat;
        }
      }
    }
  }
}
@media all and (-ms-high-contrast: none) {
  /* IE Hack */
  #socialLink ul li {
    &:nth-child(1) a {
      background-image: url("../images/iconTwitter.png");
    }

    &:nth-child(2) a {
      background-image: url("../images/iconFaceBook.png");
    }

    &:nth-child(3) a {
      background-image: url("../images/iconLinkedIn.png");
    }

    &:nth-child(4) a {
      background-image: url("../images/iconBehance.png");
    }

    &:nth-child(5) a {
      background-image: url("../images/iconGitHub.png");
    }

    &:nth-child(6) a {
      background-image: url("../images/iconInstagram.png");
    }
  }
}

footer {
  margin: 65px 0 0;
  padding: 0 0 30px;

  p {
    color: #999;
    text-shadow: 0 1px 0 #fff;
    font-family: 'Lobster', cursive;
    font-size: 1.375em;
  }
}

#motionSkipBox {
  position: fixed;
  z-index: 1000;
  right: 0;
  bottom: 0;
  display: none;
  width: 100px;
  height: 132px;

  &:hover {
    cursor: pointer;
  }

  img {
    width: 100%;

    &#motionSkipSneaker {
      position: absolute;
      z-index: 101;
      top: 0;
      left: 0;
    }

    &#motionSkipNote {
      position: absolute;
      z-index: 102;
      top: 0;
      left: 0;
    }

    &#motionSkipMask {
      position: absolute;
      z-index: 103;
      top: 0;
      left: 0;
    }
  }

  p {
    &#greeting {
      position: absolute;
      top: -65px;
      right: 0;
      white-space: nowrap;
      letter-spacing: 0;
      opacity: 0;
      color: #999999;
      font-family: 'Patrick Hand', cursive;
      font-size: 1.25em;
      font-weight: bold;
      @media screen and (-webkit-min-device-pixel-ratio: 0) {
        /* Safari Hack */
        letter-spacing: -0.05em;
      }
      @media all and (-webkit-min-device-pixel-ratio: 0) and (min-resolution: 0.001dpcm) {
        /* Chrome Hack */
        letter-spacing: 0;
      }

      &.comp {
        animation: greeting 1s linear 0.25s 1 normal;
        @keyframes greeting {
          0% {
            transform: translate(0, 0);
            opacity: 0;
          }

          50% {
            transform: translate(0, -100%);
            opacity: 1;
          }

          100% {
            transform: translate(0, -200%);
            opacity: 0;
          }
        }
      }
    }
  }

  #motionSkip {
    position: absolute;
    top: -22%;
    left: -60%;
    width: 110px;
    height: 78px;
    transition: all 0.75s ease 0.25s;
    transform: translate(0,10%);
    opacity: 0;

    &.comp {
      transition: all 0.75s ease 0.25s;
      transform: translate(0,0);
      opacity: 1;
    }

    p {
      display: table;
      width: 100%;
      height: 100%;
      margin: 0;
      border-radius: 100%;
      background: #000;

      span {
        display: table-cell;
        vertical-align: middle;
        letter-spacing: 0;
        color: #fff;
        font-family: 'Patrick Hand', cursive;
        font-size: 1.5em;
        font-weight: bold;
        @media screen and (-webkit-min-device-pixel-ratio: 0) {
          /* Safari Hack */
          letter-spacing: -0.05em;
        }
        @media all and (-webkit-min-device-pixel-ratio: 0) and (min-resolution: 0.001dpcm) {
          /* Chrome Hack */
          letter-spacing: 0;
        }
      }
    }

    &:before {
      position: absolute;
      z-index: -1;
      right: 10px;
      bottom: -5px;
      display: block;
      width: 30px;
      height: 30px;
      margin-left: 0;
      content: "";
      border-radius: 0 30px 0 30px;
      background: #000;
    }

    &:after {
      position: absolute;
      z-index: -1;
      right: 0;
      bottom: 0;
      display: block;
      width: 30px;
      height: 30px;
      margin-left: 0;
      content: "";
      -webkit-transform: rotate(30deg);
      -moz-transform: rotate(30deg);
      -ms-transform: rotate(30deg);
      -o-transform: rotate(30deg);
      transform: rotate(30deg);
      border-radius: 0 30px 0 30px;
      background: #FFF;
    }
  }
}

